        <div class="modal fade" id="modal-editar" tabindex="-1" aria-labelledby="modal-editar-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-lg modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-editarTitle"> Cadastro de Unidades de Medida</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <form class="modal-body" v-if="unidadeMedida" @submit.prevent="salvar()">
                        <div class="row">
                            <label class="col-sm-3 col-form-label">Descrição</label>
                            <div class="col-sm-9">
                                <input-componente
                                    @blur="$v.unidadeMedida.descricao.$touch"
                                    :validator="$v.unidadeMedida.descricao"
                                    v-model="unidadeMedida.descricao"
                                />
                            </div>
                            <label class="col-sm-3 col-form-label">Sigla</label>
                            <div class="col-sm-9">
