                            </div>
                            <label class="col-sm-3 col-form-label">Qtde de Casas Decimais</label>
                            <div class="col-sm-9">
                                <input-componente
                                    @blur="$v.unidadeMedida.decimais.$touch"
                                    :validator="$v.unidadeMedida.decimais"
                                    v-model="unidadeMedida.decimais"
