                            </div>
                            <div class="col-sm-12" v-if="sucessoEditar">                                
                                <mensagem-componente  :sucesso="sucessoEditar" @fechar="sucessoEditar = null"></mensagem-componente>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-sm-12 text-center">
                                <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                                <button type="button" class="btn btn-primary" :disabled="sucessoEditar || !unidadeMedida" @click.prevent="salvar()"><i class="mdi mdi-content-save font-size-18" ></i> Salvar </button>
                            </div>
                        </div>
