                </div>
            </div>
        </div>
  </div>
</template>

<script>
import { required,  maxLength, integer} from "vuelidate/lib/validators";
import { UnidadeMedidaService } from '../../../services'

const unidadeMedidaService = new UnidadeMedidaService();

export default {
    data() {
        return {
            dados: [],
            filtro:{
                ordenar: 'descricao',
                ordem: 'asc',
                page: 1,
                per_page: 2000,
                texto: '',
            },
            paginacao: {
                current_page: 0,
                // total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 2000
            },
            debounce: null,
            unidadeMedida: null,
            sucessoDeletar: null,
            erroDeletar: null,
            sucessoEditar: null,
            erroEditar: null,

        };
    },
    validations() {
        return {
            unidadeMedida: {
                descricao: { required, maxLength:maxLength(50)},
                sigla: { required, maxLength:maxLength(10)},
                decimais: { required, integer},
            }
        }
    },
    mounted() {
        this.buscar();
    },
    methods: {

        novoItem: function(obj){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.$v.unidadeMedida.$reset();
            this.unidadeMedida = {
                descricao: ''
            };            
        },

        abrirModalEditar:function(obj){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.$v.unidadeMedida.$reset();
            this.unidadeMedida = {...obj};
        },

        salvar: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.$v.unidadeMedida.$touch();
            if(this.$v.unidadeMedida.$invalid){
                return false;
            }            

            let req = this.unidadeMedida.id ? unidadeMedidaService.atualizar(this.unidadeMedida) : unidadeMedidaService.criar(this.unidadeMedida);

            req.then( response => {
                let obj = response.data;
                let index = this.dados.map(x => x.id).indexOf(obj.id);
                if(index >= 0){
                    this.dados.splice(index, 1, obj);
                }else{
                    this.dados.push(obj);
                }
                    
                this.sucessoEditar = 'Registro salvo com sucesso!';
            }).catch( erro => this.erroEditar = erro)            
        },

        abrirModalDeletar: function(obj){
            this.sucessoDeletar = null;
            this.erroDeletar = null;
            this.unidadeMedida = {...obj};
        },

        deletar: function(){
            this.sucessoDeletar = null;
            this.erroDeletar = null;

             unidadeMedidaService.deletar(this.unidadeMedida.id)
                .then( response => {
                    let i = this.dados.map(x => x.id).indexOf(this.unidadeMedida.id);
                    this.dados[i].ativo = false;
                    this.sucessoDeletar = 'Registro removido com sucesso!';
                })
                .catch( erro => this.erroDeletar = erro)            
        },

        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },

        
        buscar:function(){
            unidadeMedidaService.listar(this.filtro).then(
                    response => {
                        this.$data.dados = response.data.data;
                        
                        this.$data.paginacao = response.data;
                        this.$data.filtro.page = response.data.current_page;
                        this.$data.paginacao.per_page = response.data.per_page;
                    }
                )
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },
     
  
    }
};
</script>
